import { Resize, itemsCount, countView } from '../controllers/resizeController';
import pagination from '../views/renderNav';
import constants from '../constants';
import resizePaging from '../controllers/paginationController';

const { MAIN_BLOCK, WIDTH_OF_PREVIEW } = constants;

window.addEventListener('resize', () => {
    const width = document.body.clientWidth;
    const resizeWindow = new Resize();
    resizeWindow.countPreviewsCalc();
    const activePage = document.querySelector('.active').dataset.scrollTo;
    const firstEl = document.getElementById(activePage);
    const indent = (width - (countView * WIDTH_OF_PREVIEW)) / (2 * countView);

    MAIN_BLOCK.style.transition = 'transform 0s ease';
    MAIN_BLOCK.style.transform = `translateX(${-(firstEl.offsetLeft - indent)}px)`;

    pagination.renderNav(itemsCount);
    resizePaging.scrollNav();
});



