import { Resize, itemsCount } from './resizeController';
import createRequest from './mainController';
import pagination from '../views/renderNav';

class NewVideo {
    addNewVideo() {  // eslint-disable-line
        const paging = document.querySelectorAll('.pagination');
        const lastPage = paging[paging.length - 1];
        const preLastPage = paging[paging.length - 2];
        let resizeWindow;

        if (lastPage.classList.contains('active') || preLastPage.classList.contains('active')) {
            createRequest();
            resizeWindow = new Resize();
            resizeWindow.addNewVideoPart();
            resizeWindow.countPreviewsCalc();
            pagination.renderNav(itemsCount);
        }
    }
}

export default new NewVideo();



