import constants from '../constants';
import resizeWindow from '../controllers/paginationController';

const { NAV_LIST } = constants;

NAV_LIST.addEventListener('click', (e) => {
    const target = e.target;
    if (target.hasAttribute('data-scroll-to')) {
        resizeWindow.countPageCalc(target);
    }
});



