import constants from '../constants';

const { MAIN_BLOCK } = constants;

let countView;  // eslint-disable-line
let itemsCount = 15;  // eslint-disable-line

class Resize {
    countPreviewsCalc() { // eslint-disable-line
        const width = document.body.clientWidth;
        if (width < 750) {
            MAIN_BLOCK.style.width = `calc(100vw*${itemsCount})`;
            countView = 1;
        } else if (width < 1200) {
            MAIN_BLOCK.style.width = `calc(50vw*${itemsCount})`;
            countView = 2;
        } else {
            MAIN_BLOCK.style.width = `calc(33vw*${itemsCount})`;
            countView = 3;
        }
    }

    addNewVideoPart() { // eslint-disable-line
        itemsCount += 15;
    }
}

export { Resize, countView, itemsCount };



