import GetVideo from './getVideoController';
import view from '../views/renderVideo';
import constants from '../constants';

const { MAIN_BLOCK, INPUT } = constants;

let nextPageToken = '';

function normalizeObj(data) {
    const snippetPath = data.snippet;
    return {
        previewUrl: snippetPath.thumbnails.medium.url,
        previewTitle: snippetPath.title,
        previewDate: snippetPath.publishedAt.substring(0, 10),
        previewAuthor: snippetPath.channelTitle,
        previewId: data.id.videoId,
        previewViewers: '',
        previewDesc: snippetPath.description
    };
}

function createRequest() {
    const videoList = new GetVideo();
    const query = INPUT.value || 'the+rolling+scopes';
    let str = '';
    const normalizedObjArr = [];
    videoList.requestMain(query, nextPageToken)
        .then((res) => {
            const idArr = [];
            nextPageToken = res.nextPageToken;
            for (let i = 0; i < res.items.length; i += 1) {
                const normilizedObj = normalizeObj(res.items[i]);
                normalizedObjArr.push(normilizedObj);
                idArr.push(normilizedObj.previewId);
            }
            return idArr.join(',');
        })
        .then((obj) => {
            videoList.requestStatistics(obj)
            .then((data) => {
                for (let i = 0; i < data.items.length; i += 1) {
                    normalizedObjArr[i].previewViewers = data.items[i].statistics.viewCount;
                    str += view.renderItems(normalizedObjArr[i]);
                }
                return str;
            })
            .then((htmlStr) => {
                MAIN_BLOCK.innerHTML += htmlStr;
            });
        });
}

export default createRequest;



