import { countView } from '../controllers/resizeController';
import constants from '../constants';

const { NAV_LIST } = constants;
let prevCountView;

class Pagination {

    renderNav(countOnPage) { // eslint-disable-line
        const countPaginationItems = document.querySelectorAll('.pagination').length;
        const activePage = document.querySelector('.active');
        let str = '';
        let activePageData;

        if (countView === prevCountView
            && Math.floor(countOnPage / countView) === countPaginationItems) {
            return;
        }

        if (activePage) {
            activePageData = activePage.getAttribute('data-scroll-to');
        }

        for (let i = 1; i <= countOnPage / countView; i += 1) {
            str += `<li><a href="#" class="pagination" data-scroll-to="${((i - 1) * countView) + 1}">${i}</a></li>`;
        }
        NAV_LIST.innerHTML = str;

        if (!activePage) {
            NAV_LIST.firstElementChild.firstElementChild.classList.add('active');
            activePageData = 1;
        }
        const navItems = document.querySelectorAll('.pagination');
        const newActivePage = Math.ceil(activePageData / countView);
        navItems[newActivePage - 1].classList.add('active');
        prevCountView = countView;
    }
}

export default new Pagination();



