import constants from '../constants';
import { countView } from '../controllers/resizeController';
import addVideo from '../controllers/addVideoController';

const { WIDTH_OF_PREVIEW, MAIN_BLOCK, NAV_LIST, WIDTH_OF_NAV, WIDTH_PAGINATION } = constants;

class PaginationController {
    countPageCalc(el) {
        const prevActive = document.querySelector('.active');
        this.nextPage(el);
        prevActive.classList.remove('active');
        el.classList.add('active');
        addVideo.addNewVideo();
        this.scrollNav();
    }

    nextPage(el) { // eslint-disable-line
        const width = document.body.clientWidth;
        const firstEl = document.getElementById(el.dataset.scrollTo);
        const indent = (width - (countView * WIDTH_OF_PREVIEW)) / (2 * countView);
        const coordX = firstEl.offsetLeft - indent;

        MAIN_BLOCK.style.transition = 'transform 1s ease';
        MAIN_BLOCK.style.transform = `translateX(${-coordX}px)`;
    }

    scrollNav() { // eslint-disable-line
        const activePage = document.querySelector('.active').parentNode;
        const coordX = (activePage.offsetLeft - (WIDTH_OF_NAV / 2)) + (WIDTH_PAGINATION / 2);

        NAV_LIST.style.transform = `translateX(${-coordX}px)`;
    }
}

export default new PaginationController();



