import 'whatwg-fetch';
import './views/renderLayout';
import constants from './constants';
import createRequest from './controllers/mainController';
import { Resize, itemsCount } from './controllers/resizeController';
import pagination from './views/renderNav';
import './events/hoverNavEvent';
import './events/switchPageEvent';
import './events/swipeEvent';
import './events/resizeEvent';

const { BUTTON, MAIN_BLOCK, NAV_LIST } = constants;

function loadVideos() {
    MAIN_BLOCK.innerHTML = '';
    NAV_LIST.innerHTML = '';
    createRequest();

    const resize = new Resize();
    resize.countPreviewsCalc();

    pagination.renderNav(itemsCount);
}

BUTTON.addEventListener('click', () => {
    loadVideos();
});

document.addEventListener('keydown', (e) => {
    if (e.keyCode === 13) {
        loadVideos();
    }
});




