import constants from '../constants';
import { countView } from '../controllers/resizeController';
import resizeWindow from '../controllers/paginationController';
import addVideo from '../controllers/addVideoController';

const { WRAP } = constants;
let coordStart = null;
let coordEnd = null;

WRAP.addEventListener('mousedown', (eDown) => {
    eDown.preventDefault();
    coordStart = eDown.clientX;
});

WRAP.addEventListener('mouseup', (eUp) => {
    const prevActive = document.querySelector('.active');
    let newActive;
    coordEnd = eUp.clientX;

    if (coordEnd - coordStart > 15) {
        newActive = prevActive.parentElement.previousElementSibling.firstElementChild;
    } else if (coordEnd - coordStart < -15) {
        newActive = prevActive.parentElement.nextElementSibling.firstElementChild;
    }
    resizeWindow.nextPage(newActive);
    prevActive.classList.remove('active');
    newActive.classList.add('active');
    addVideo.addNewVideo(countView);
    resizeWindow.scrollNav();
});

WRAP.addEventListener('touchstart', (e) => {
    e.preventDefault();
    e.stopPropagation();
    coordStart = e.changedTouches[0];
}, false);

WRAP.addEventListener('touchend', (e) => {
    e.preventDefault();
    e.stopPropagation();
    const prevActive = document.querySelector('.active');
    let newActive;
    coordEnd = e.changedTouches[0];

    if (coordEnd.pageX - coordStart.pageX > 15) {
        newActive = prevActive.parentElement.previousElementSibling.firstElementChild;
    } else if (coordEnd.pageX - coordStart.pageX < -15) {
        newActive = prevActive.parentElement.nextElementSibling.firstElementChild;
    }
    resizeWindow.nextPage(newActive);
    prevActive.classList.remove('active');
    newActive.classList.add('active');
    addVideo.addNewVideo(countView);
    resizeWindow.scrollNav();
});



